.service-head-bg {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), url('../assets/images/pageheads/servicehead.jpg');
    background-size: cover;
}

.service-content {
    color: #000;
    font-weight: 300;
}

.service-list {
    text-align: left !important;
}

.service-list li {
    margin-bottom: 0.5rem;
}

.service-list li::before {
    content: "\2022"; 
    color: #1E4258; 
    font-weight: bold; 
    display: inline-block; 
    width: 1em; 
    margin-left: 0em; 
}

