/* carousel  */

.main-image {
    max-height: 700px; 
}

.carousel-indicators {
    margin-bottom: 8rem;
}

.carousel-indicators li {
    background-color: #1E4258 !important;
    height: 5px;
}

.carousel-indicators .active {
    background-color: #fff !important;
}


@media (max-width: 768px) {
    .carousel-item {
        height: 600px !important;
    }
    
    .carousel-item img {
        height: 100%;
        object-fit: cover !important; 
    }  
}

/* quote button  */

.landing-button {
    position: absolute; 
    bottom: 0;
    margin-bottom: 10rem;
    z-index: 2;
}

/* headings & text */


.landing-section {
    color: #fff;
    text-align: center;
    padding: 4.5rem 0 6rem;
}

.section-head {
    margin-bottom: 2rem;
}

.section-head h1 {
    font-size: 3rem;
    font-weight: 700;
    letter-spacing: 0.02em;
    line-height: 1;
    margin: 0 !important;
}

.section-head h3 {
    font-size: 1.25rem !important; 
    font-weight: 300;
}

.landing-section .break-line {
    margin: 0 auto 2rem;
}

.section-copy {
    margin-bottom: 3.5rem;
}

.section-copy p {
    font-weight: 300;
    line-height: 23px;
    font-size: 1.2rem;
}

@media (max-width: 768px) {
    .section-head h1 {
        font-size: 2.5rem;
    }

    .service-slide {
        flex-direction: column;
    }

    .service-container {
        margin-bottom: 1rem;
    }
}

/*  services  */

.slide-container {
    position: relative;
    height: 240px;
    margin-bottom: 2rem;
}

.first-slide, .second-slide {
    position: absolute;
    
}

.second-slide {
    display: none;
}


.service-slide {
    display: flex;
    justify-content: space-between;
    flex-direction: row;

}

.service-container .icon {
    margin-bottom: 1rem;
}

.icon svg {
    transition: width 0.2s linear;
    transition: height 0.2s linear;
}

.icon svg:hover {
    height: 165px;
    width: 165px;
}

.service-indicators {
    position: relative;
    margin-bottom: 0; 
}

.landing-service-button {
    background-color: #234C66;
    border: 1px solid #fff !important; 
    margin-bottom: 1rem;
    transition: background-color 0.5s linear;

}

.landing-service-button:hover {
    background: rgba(63, 132, 196, 0.6);
}

@media (max-width: 768px) {
    .slide-container {
        height: auto;
    }

    .first-slide, .second-slide {
        position: relative;
        display: block !important;
    }

    .service-slide {
        flex-direction: column;
    }
}






/* latest projects */

.landing-gallery {
    display: flex;
    flex-flow: row wrap;
}

.image-container{
    flex: 1 1 25%;
    padding: 0.10rem;

    margin: 0;
}

.landing-gallery figure::after {
    content: '';
    display: block; 
    padding-top: 100%;
}

.image-container .inner-image {
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100%;
    width: 100%;
}

.inner-image img {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
}

.inner-image figcaption {
    display: flex;
    flex-direction: column;
    justify-content: center;

    position: absolute;
    z-index: 2;
    width: 100%;

    height: 100%;

    font-weight: 300;

    background-color: rgb(2 93 150 / 60%); 
    opacity: 0;
    transition: opacity 0.2s linear;
}

.hover-active {
   opacity: 1 !important;
}


@media (max-width: 768px) {
    .landing-gallery figure {
        flex: 1 1 50%;
    }  
}

@media (max-width: 576px) {
    .landing-gallery figure {
        flex: 1 1 100%;
    }  
}


/* accreditation  */

.accred-bg {
    background-image: url('../assets/images/landing/accreditation/bg.png');  
    background-size: cover; 
    background-position: center;
}

.landing-section .accreditation {
    text-align: left;
}

.accreditation header {
    margin-bottom: 2rem;
}

.accreditation h1 {
    font-weight: 700;
    font-size: 4rem;
    color: #333;
}

.accreditation p {
    color: #717171;
}

.cat-container {
    margin-bottom: 2rem;
}

.cat-logo {
    height: 150px;
    width: 500px;
}

.review-screen {
    height: auto; 
    max-height: 150px;
    width: 100%;
    background-color: #1E4258;
    padding: 3px;
    border-radius: 10px
}

.cat-indicators {
    margin-bottom: 0; 
    position: relative;
}



@media (max-width: 768px) {
    .accred-bg {
        background-image: url('../assets/images/landing/accreditation/bg-mob.png');  
    }

    .accreditation header {
        margin-bottom: 2rem;
    }
    
    .accreditation h1 {
        font-weight: 700;
        font-size: 2rem;
        color: #333;
    }

    .accreditation p {
        font-size: 1rem;
    }

    .cat-item-mob {
        height: auto !important;
    }


    .cat-logo {
        width: 295px;
        height: 84px !important;
    }
}










