.top-bar li, .bottom-bar li, .top-bar a, .bottom-bar a, .nav-mobile a  {
    color: #fff; 
    opacity: 1;
    transition: opacity 0.2s linear;
}

.top-bar li:hover, .bottom-bar li:hover, .top-bar a:hover, .bottom-bar a:hover, .nav-mobile a:hover {
    text-decoration: none;
    opacity: 0.8;
}

.main-nav {
    position: absolute;
    width: 100%;
    z-index: 16;
}

.nav-down {
    position: relative; 
}

/* top bar */

.top-bar {
    padding: 3rem 0 2rem;
}

.tb-logo {
    text-align: center;
}

.tb-contact p {
    margin-bottom: 0.5rem;
}

.tb-phone {
    font-size: 1.5rem;   
}

/* bottom bar */

.bottom-bar {
    padding: 1rem 0;

}

.bg-blue {
    background: rgba(30, 66, 88) !important;  
}

.nav-bar, .tb-socials {
    text-align: right;
}

.nav-bar li, .tb-socials a {
    margin-left: 2rem; 

}

.nav-bar ul {
    margin-bottom: 0;
}

.nav-bar li {
    display: inline-block;
}

.bb-mobile {
    display: flex; 
    justify-content: space-between;
}

/* hamburger  */

.hamburger {
    padding: 0 !important;
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    background-color: #fff !important;
    
}

.hamburger:hover {
    opacity: 1 !important;
}

/* small screens menu */

.nav-mobile {
    display: none !important;
    padding: 3rem 0 3rem;
    background-color: rgba(30, 66, 88);
}

.nm-active {
    display: flex !important; 
}

.nav-mobile nav {
    flex: 0 1 auto;
}

.nav-mobile p {
    font-size: 1.5rem;
}

.nav-mobile li {
    display: block;
    margin-bottom: 1.25rem;
    color: #fff; 
}



.nav-mobile .links {
    margin-bottom: 0.75rem;
}

.nm-socials {
    display: flex;
}

.nm-socials a {
    margin-right: 2rem; 
    margin-bottom: 0;
}



/* breakpoints */

@media (max-width: 768px) {
    .top-bar {
        padding: 1rem;
    }

    .tb-contact {
        display: flex;
        justify-content: space-between;

        padding: 0 !important;
    }

    .tb-contact p {
        display: inline;
    }

    .tb-phone {
        font-size: 1rem;   
    }
}