.contact-head-bg {
    background: linear-gradient(360deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), url('../assets/images/pageheads/contacthead.jpg');
    background-size: cover;
}

.contact.page-content {
    color: #fff;
    font-size: 1.25rem;

    margin: 0 -15px;
    padding: 2rem 15px 4rem;
}

/* contact list */

.map-icon {
    margin-bottom: 3rem;
}

.contact-list .address span {
    float: left;
}

.contact-list .address p {
    display: inline-block;
}

.contact-list .icon {
    margin-right: 2rem;
}

.contact-list li {
    margin-bottom: 2rem;
}

/* form  */

.form-head {
    margin-bottom: 1rem;
    
    
}

.form-head h4 {
    margin-bottom: 1rem;
    font-size: 1.25rem !important;
}

.input-fields {
    margin-bottom: 3rem;
}

.form-input {
    margin-bottom: 1.25rem;
}

#name, #email {
    height: 2rem;
}

#message {
    height: 250px;
}

.form-input label {
    display: block;
}

.form-input input, .form-input textarea {
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    font-weight: 300;
    font-size: 0.9rem;
    width: 100%; 
    border: none;
    border-radius: 5px;
}

.form-input input:focus, .form-input textarea:focus {
    outline: 1px solid #fff;
    -moz-outline-radius: 5px;
}

.form-input .error {
    background: rgb(255 153 153 / 20%) !important;
    
}



.contact-form button {
    background-color: #234C66;
    border: 1px solid #fff !important; 
    transition: background-color 0.5s linear;
}

.contact-form button:hover {
    background: rgba(63, 132, 196, 0.6);
}

.error-message {
    font-size: 0.5rem; 
    font-style: italic; 
    color: red;
}
/* breakpoints */

@media (max-width: 370px) { 
    .contact.page-content {
        font-size: 1rem;
    }    
}