@font-face {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local('Roboto Condensed Bold'), local('RobotoCondensed-Bold'), url(https://fonts.gstatic.com/s/robotocondensed/v18/ieVi2ZhZI2eCN5jzbjEETS9weq8-32meGCQYb9lecyU.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local('Roboto Condensed'), local('RobotoCondensed-Regular'), url(https://fonts.gstatic.com/s/robotocondensed/v18/ieVl2ZhZI2eCN5jzbjEETS9weq8-19K7DQk6YvM.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
    font-family: 'Roboto Condensed';
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local('Roboto Condensed Light'), local('RobotoCondensed-Light'), url(https://fonts.gstatic.com/s/robotocondensed/v18/ieVi2ZhZI2eCN5jzbjEETS9weq8-33mZGCQYb9lecyU.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

/* global */

body {
    font-family: 'Roboto Condensed'!important;
    font-weight: 400;
    font-size: 1.1rem;
    letter-spacing: 0.02em;
    line-height: 21px;
}

.container-fluid {
    max-width: 1111px !important;
}

.v-align {
    display: flex;
    align-items: center;
}

h2 {
    font-size: 2.5rem;
}

button {
    padding: 0px;
}

button:focus {
    outline: none !important;
}

.break-line {
    width: 2rem;
    
    border: 1px solid #B08241;
}

button {
    font-size: 1.25rem;
    color: #fff;
    padding: 1rem 2rem;
    border: none !important;
}

.button-container {
    display: flex;
    justify-content: center;
    width: 100%;
}

.button-1 {
    background-color: rgba(30, 66, 88);
    opacity: 0.8;
    transition: opacity 0.2s linear; 
}

.button-1:hover {
    opacity: 1;
}

/* backgrounds */

.bg-blue-1 {
    background-color: #1E4258;
}

.bg-blue-1-fade {
    background: rgba(30, 66, 88, 0.4);
}

.bg-blue-2 {
    background-color: #234C66;
}

.bg-blue-3 {
    background-color: #163040;
}

.bg-blue-4 {
    background-color: rgba(35, 76, 102, 0.05);
}
.inset-shadow {
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.gallery-head-bg {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), url('./assets/images/pageheads/galleryhead.jpg');
    background-size: cover;
}


/* page headers */

.page-head {
    width: 100%;
    text-align: center;

    padding: 8rem 0 4rem;
}

.page-head h1 {
    margin-bottom: 2rem;
}

.ph-break-line {
    border: 1px solid #1E4258; 
    width: 3rem;

    margin: 0 auto 2rem;
}


